import React from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
// Import Swiper styles
import "swiper/css";
import { ProjectData } from "./Projects";
// import required modules
import { EffectCards } from "swiper";
import { useLocation } from "react-router-dom";

function ProjectInformation(props) {
  const location = useLocation();
  const selectedProject = location.state.project ?? ProjectData[0];
  window.previousLocation = "projects";
  /* useEffect(() => {
    return () => {
      const projectsEle = document.getElementById("projects-section");
      projectsEle.scrollIntoView();
      console.log("ROute");
    };
  }); */

  return (
    <div className="project-info ">
      <div className="project-info-container">
        <div className="project-info-title">
          Project : {selectedProject.name}
        </div>
        <div className="project-info-images">
          <Swiper
            effect={"cards"}
            grabCursor={true}
            modules={[EffectCards]}
            className="project-info-images-swiper"
          >
            {selectedProject.images.map((val, index) => (
              <SwiperSlide key={index}>
                <div className="project-info-images-slide">
                  <img src={val} alt="Project" />
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
        <div className="project-info-text">
          <div className="project-info-text-data">
            {selectedProject.description}
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProjectInformation;
