import React from "react";
import LazyFallbackLoader from "./Components/Common/LazyFallbackLoader";
// import Home from "./Components/Home";
import { Routes, Route } from "react-router-dom";
import { HOME_ROUTE, PROJECT_INFO } from "./routes";
import ProjectInformation from "./Components/ProjectInformation";
// import { createBrowserHistory as createHistory } from "history";
// import { Router } from "react-router";
// GSAP
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import ScrollToPlugin from "gsap/ScrollToPlugin";
// import { useEffect } from "react";
// import Home from "./Components/Home";
const LazyHome = React.lazy(() => import("./Components/Home"));
// export const historyObject = createHistory();
// history={historyObject}

function App(props) {
  gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);
  return (
    <React.Suspense fallback={<LazyFallbackLoader />}>
      <div className="app__container">
        <Routes>
          <Route path={HOME_ROUTE} element={<LazyHome />} />
          <Route path={PROJECT_INFO} element={<ProjectInformation />} />
        </Routes>
      </div>
    </React.Suspense>
  );
}

export default App;
