import React, { Component } from "react";
import bp1 from "../Assets/Projects/boompanda/bp1.png";
import bp2 from "../Assets/Projects/boompanda/bp2.png";
import bp4 from "../Assets/Projects/boompanda/bp4.png";
import k1 from "../Assets/Projects/kokoloco/k1.png";
import c2w1 from "../Assets/Projects/core2web/testimonials.png";
import zw1 from "../Assets/Projects/zealwise/zw1.png";
import { Link } from "react-router-dom";
import { Navigate } from "react-router-dom";
// import gsap from "gsap";

export const ProjectData = [
  {
    name: "Boompanda",
    images: [bp1, bp2, bp4],
    link: "https://www.boompanda.in/",
    description:
      "Earn through some interesting tasks, offers, events and many more",
    details:
      "Multi user system, Developed User website & dashboard to manage data ",
  },
  {
    name: "Kokoloco",
    images: [k1],
    link: "https://www.kokoloco.com/",
    description: "E-commerce website to buy and sell products",
  },
  {
    name: "Zealwise Technologies PVT. LTD. ",
    images: [zw1],
    link: "http://www.zealwise.com/",
    description: "Portfolio which highlights company's unique selling point",
  },
  {
    name: "Core2web",
    images: [c2w1],
    link: "https://www.core2web.in/",
    description:
      "E-learning Platform : Assembles all learning modes like lectures, QA Forum, Compiler, Courses",
  },
];

class Projects extends Component {
  state = {
    selectedProject: null,
  };

  componentDidMount() {
    /*   if (window.previousLocation === "projects") {
      const goTOProjects = document.getElementById("view-projects");
      goTOProjects.click();
      window.previousLocation = "";
      console.log("Scroll to projects");
      window.location.reload();
    } */
    /* if (window.location.hash === "#projects") {
      const projectsEle = document.getElementById("projects-section");
      projectsEle?.scrollIntoView();
      console.log("MOUNTED", projectsEle);
    } */
    // goTOProjects.click();
    /* gsap.to(window, {
      duration: 1.5,
      ease: "power4.inOut",
      scrollTo: 1747,
    }); */
  }

  openProjectInfo = (selectedProject) => {
    Navigate({
      to: `/${selectedProject.name}`,
      state: {
        selectedProject,
      },
    });
    this.setState({ selectedProject });
  };

  render() {
    const { selectedProject } = this.state;
    return (
      <div className="projects" id="projects-section">
        <div className="projects__container">
          <div className="projects__header section-title">
            <div className="square"></div>
            My Recent Work
          </div>
          <div className="title-description">
            Here are few shareable projects I've worked on.
          </div>
          <div className="projects__list">
            {ProjectData.map((val, index) => {
              return (
                <div className="projects__list__card" key={index}>
                  <img
                    src={val.images[0]}
                    alt={val.name}
                    className="projects__list__card__image"
                  />
                  <div className="projects__list__card__overlay">
                    <div className="name">{val.name}</div>
                    <div className="description">{val.description}</div>
                    <Link to={`${val.name}`} state={{ project: val }}>
                      <div className="visit-link">
                        View More
                        <img src="/assets/right-arrow.svg" alt="" />
                      </div>
                      {/* <div
                        className="eye"
                        onClick={() => this.openProjectInfo(val)}
                    >
                      <img src="/assets/solid-blue-eye.svg" alt="" />
                    </div> */}
                    </Link>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
        {selectedProject && this.renderProjectInformation()}
      </div>
    );
  }
}

export default Projects;

